// Цвета
$background-white: #fff;
$background-pink: #ffb8ca;
$background-mobile-menu-color: #e17992;
$background-secondary-color: #fff5f6;
$text-black: #000;
$text-secondary: #907e82;
$text-pink: #ffa5ba;
$text-red: #d41443;
$background-one-color: #ffb8ca;
$background-two-color: #f0d1a5;
$background-three-color: #c2e297;
$border-color: #e1e1e1;
$color-button: #d41443;
$color-hover-button: #c7133e;
$cafe-text-color: #f0d1a5;
$social-icons-fill: #fea5bb;
$hover-social-icons-fill: #d41443;
$hover-social-icons-color: #fff;
$hover-buttons-red: #d41342;
$hover-buttons-gray: #ecebeb;
$mobile-menu-border-color: #e18298;
// анимашки
$transition-options: 250ms cubic-bezier(0.4, 0, 0.2, 1);
$slideInFromLeft: 1s ease-out 0s 1 slideInFromLeft;
$slideInFromRight: 1s ease-out 0s 1 slideInFromRight;

@keyframes slideInFromRight {
  0% {
    transform: translateX(50%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes moveInBottom {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }

  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}
@keyframes slideInFromLeft {
  0% {
    transform: translateX(-50%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
